      text:
        'Companies are the makers of their fortunes, and like all of us, they are utterly exposed to serendipity. That should make them more humble, more vulnerable and more human. At the end of the day, as hyperconnectivity and transparency expose companies\' behavior in broad daylight, staying true to their true selves is the only sustainable value proposition. Or as the ballet dancer Alonzo King said, "What\'s interesting about you is you." For the true selves of companies to come through, openness is paramount, but radical openness is not a solution, because when everything is open, nothing is open. "A smile is a door that is half open and half closed," the author Jennifer Egan wrote. Companies can give their employees and customers more control or less. They can worry about how much openness is good for them, and what needs to stay closed. Or they can simply smile, and remain open to all possibilities. Thank you.',
