      submittedBy: 'jason.carmel@possible.com',
      text:
        "Companies can give people more control, but they can also give them less control. Traditional business wisdom holds that trust is earned by predictable behavior, but when everything is consistent and standardized, how do you create meaningful experiences? Giving people less control might be a wonderful way to counter the abundance of choice and make them happier. Take the travel service Nextpedition. Nextpedition turns the trip into a game, with surprising twists and turns along the way. It does not tell the traveler where she's going until the very last minute, and information is provided just in time. Similarly, Dutch airline KLM launched a surprise campaign, seemingly randomly handing out small gifts to travelers en route to their destination. U.K.-based Interflora monitored Twitter for users who were having a bad day, and then sent them a free bouquet of flowers. \n\nIs there anything companies can do to make their employees feel less pressed for time? Yes. Force them to help others. A recent study suggests that having employees complete occasional altruistic tasks throughout the day increases their sense of overall productivity. At Frog, the company I work for, we hold internal speed meet sessions that connect old and new employees, helping them get to know each other fast. By applying a strict process, we give them less control, less choice, but we enable more and richer social interactions. ",
