      submittedBy: 'jason.carmel@possible.com',
      text:
        "Companies are losing control. What happens on Wall Street no longer stays on Wall Street. What happens in Vegas ends up on YouTube. (Laughter) Reputations are volatile. Loyalties are fickle. Management teams seem increasingly disconnected from their staff. (Laughter) A recent survey said that 27 percent of bosses believe their employees are inspired by their firm. However, in the same survey, only four percent of employees agreed. Companies are losing control of their customers and their employees. But are they really? \n\nI'm a marketer, and as a marketer, I know that I've never really been in control. Your brand is what other people say about you when you're not in the room, the saying goes. Hyperconnectivity and transparency allow companies to be in that room now, 24/7. They can listen and join the conversation. In fact, they have more control over the loss of control than ever before. They can design for it. But how? ",
      title: '3 ways to (usefully) lose control of your brand',
      uid: '89IV4qglRZTE9Ty8y9113yC7w9A3',
      videoEndTime: '00:01:21',
      videoStartTime: '00:00:13',
