      created: {
        seconds: 1572633750,
        nanoseconds: 61000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "What you'll see, I think, is two things that are critical, and these, I think, are two challenges for the open movement. The first is: can we really survive on volunteers? If this is so critical, do we not need it funded, organized, supported in much more structured ways? I think the idea of creating the Red Cross for information and knowledge is a fantastic idea, but can we really organize that, just on volunteers? What kind of changes do we need in public policy and funding to make that possible? What's the role of the BBC, for instance, in that world? What should be the role of public policy? And finally, what I think you will see is the intelligent, closed organizations moving increasingly in the open direction. So it's not going to be a contest between two camps, but, in between them, you'll find all sorts of interesting places that people will occupy. New organizational models coming about, mixing closed and open in tricky ways. It won't be so clear-cut; it won't be Microsoft versus Linux -- there'll be all sorts of things in between. And those organizational models, it turns out, are incredibly powerful, and the people who can understand them will be very, very successful. \n\nLet me just give you one final example of what that means. I was in Shanghai, in an office block built on what was a rice paddy five years ago -- one of the 2,500 skyscrapers they've built in Shanghai in the last 10 years. And I was having dinner with this guy called Timothy Chan. Timothy Chan set up an Internet business in 2000. Didn't go into the Internet, kept his money, decided to go into computer games. He runs a company called Shanda, which is the largest computer games company in China. Nine thousand servers all over China, has 250 million subscribers. At any one time, there are four million people playing one of his games. How many people does he employ to service that population? 500 people. Well, how can he service 250 million people from 500 employees? Because basically, he doesn't service them. He gives them a platform; he gives them some rules; he gives them the tools and then he kind of orchestrates the conversation; he orchestrates the action. But actually, a lot of the content is created by the users themselves. And it creates a kind of stickiness between the community and the company which is really, really powerful. \n\nThe best measure of that: so you go into one of his games, you create a character that you develop in the course of the game. If, for some reason, your credit card bounces, or there's some other problem, you lose your character. You've got two options. One option: you can create a new character, right from scratch, but with none of the history of your player. That costs about 100 dollars. Or you can get on a plane, fly to Shanghai, queue up outside Shanda's offices -- cost probably 600, 700 dollars -- and reclaim your character, get your history back. Every morning, there are 600 people queuing outside their offices to reclaim these characters. (Laughter) So this is about companies built on communities, that provide communities with tools, resources, platforms in which they can share. He's not open source, but it's very, very powerful. ",
