      submittedBy: 'jason.carmel@possible.com',
      text:
        "Let me just give you two examples. The first is: imagine yourself going to a venture capitalist and saying, \"I've got a fantastic idea. I've invented this brilliant new program that is much, much better than Microsoft Outlook.\" Which venture capitalist in their right mind is going to give you any money to set up a venture competing with Microsoft, with Microsoft Outlook? No one. That is why the competition with Microsoft is bound to come -- will only come -- from an open-source kind of project. \n\nSo, there is a huge competitive argument about sustaining the capacity for open-source and consumer-driven innovation, because it's one of the greatest competitive levers against monopoly. There'll be huge professional arguments as well. Because the professionals, over here in these closed organizations -- they might be academics; they might be programmers; they might be doctors; they might be journalists -- my former profession -- say, \"No, no -- you can't trust these people over here.\" ",
