      submittedBy: 'jason.carmel@possible.com',
      text:
        "This is the phrase that I've used in some stuff which I've done with a think tank in London called Demos, where we've been looking at these people who are amateurs -- i.e., they do it for the love of it -- but they want to do it to very high standards. And across a whole range of fields -- from software, astronomy, natural sciences, vast areas of leisure and culture like kite-surfing, so on and so forth -- you find people who want to do things because they love it, but they want to do these things to very high standards. They work at their leisure, if you like. They take their leisure very seriously: they acquire skills; they invest time; they use technology that's getting cheaper -- it's not just the Internet: cameras, design technology, leisure technology, surfboards, so on and so forth. Largely through globalization, a lot of this equipment has got a lot cheaper. More knowledgeable consumers, more educated, more able to connect with one another, more able to do things together. Consumption, in that sense, is an expression of their productive potential. Why, we found, people were interested in this, is that at work they don't feel very expressed. They don't feel as if they're doing something that really matters to them, so they pick up these kinds of activities. This has huge organizational implications for very large areas of life. \n\nTake astronomy as an example, which Yochai has already mentioned. Twenty years ago, 30 years ago, only big professional astronomers with very big telescopes could see far into space. And there's a big telescope in Northern England called Jodrell Bank, and when I was a kid, it was amazing, because the moon shots would take off, and this thing would move on rails.  And it was huge -- it was absolutely enormous. Now, six amateur astronomers, working with the Internet, with Dobsonian digital telescopes -- which are pretty much open source -- with some light sensors developed over the last 10 years, the Internet -- they can do what Jodrell Bank could only do 30 years ago. So here in astronomy, you have this vast explosion of new productive resources. The users can be producers. ",
