      created: {
        seconds: 1572631699,
        nanoseconds: 465000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "So this is a huge challenge to the way we think creativity comes about. The traditional view, still enshrined in much of the way that we think about creativity -- in organizations, in government -- is that creativity is about special people: wear baseball caps the wrong way round, come to conferences like this, in special places, elite universities, R&amp;D labs in the forests, water, maybe special rooms in companies painted funny colors, you know, bean bags, maybe the odd table-football table. Special people, special places, think up special ideas, then you have a pipeline that takes the ideas down to the waiting consumers, who are passive. They can say \"yes\" or \"no\" to the invention. That's the idea of creativity. What's the policy recommendation out of that if you're in government, or you're running a large company? More special people, more special places. Build creative clusters in cities; create more R&amp;D parks, so on and so forth. Expand the pipeline down to the consumers. \n\nWell this view, I think, is increasingly wrong. I think it's always been wrong, because I think always creativity has been highly collaborative, and it's probably been largely interactive. But it's increasingly wrong, and one of the reasons it's wrong is that the ideas are flowing back up the pipeline. The ideas are coming back from the consumers, and they're often ahead of the producers. Why is that? Well, one issue is that radical innovation, when you've got ideas that affect a large number of technologies or people, have a great deal of uncertainty attached to them. The payoffs to innovation are greatest where the uncertainty is highest. And when you get a radical innovation, it's often very uncertain how it can be applied. ",
