      created: {
        seconds: 1572630222,
        nanoseconds: 333000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "So why are screens so pervasive in our analog life? We see screens from the moment we wake up, to the moment we fall asleep. Why? \n\nI think it's because computers give us superpowers. Within the digital universe, we have the power to displace space and the power to displace time. It doesn't matter if you're using technology for entertainment, productivity or communication. Think of it this way: let's all go home tonight and watch our favorite show on television. This is theater -- time and space displaced. As soon as I'm done with this TED Talk, I'm going to immediately call my lovely family in Seattle. That's displacement of space. Now, these are such great superpowers that we put up with the two-dimensional limitations of our current digital world. But what if we didn't have to? What if we could have these same digital powers in our world? You can already see glimmers of this, but I believe our children's children will grow up in a world devoid of 2D technology. It's remarkable to dream of this world, a world where technology truly understands us -- where we live, work and communicate -- with tools that enhance the human experience, not machines that limit our humanity. ",
