      created: {
        seconds: 1572630142,
        nanoseconds: 17000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "Now, as I put this HoloLens on, let me explain the setup a little bit. It's probably the most risky demo we have ever done on any stage with HoloLens, and I can't think of a better place to do it than here at TED. Momentarily, I am going to be seeing holograms right on this stage, just as clearly as I can see all of you. Now at the same time, we have also this special camera that just walked in onstage so that all of you can share in this experience with me up on all the monitors. \n\nSo let's start our journey. And what better place to begin our journey, than in the computer cave of 2D. Let's explore the world all around us with this new lens, and understand the computer world from a brand new perspective. \n\nThe computer universe is both marvelous and primitive. It's a universe based on causality. As developers, we dream the different causes and then we program the different effects. Double click on an icon, that's a cause. Open an application, that's an effect. Now when we compare this to our physical universe, it is overly constraining, because our universe is not digital. Our universe is analog. Our universe doesn't think in terms of zero or one, true or false, or black or white. We exist in a world governed by quantum physics, a universe of zero and one both at the same time, a reality based on infinite probabilities and shades of gray. You can see how these two worlds collide. ",
