      created: {
        seconds: 1571268163,
        nanoseconds: 728000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "We're also embedding sensing and smart materials into the synthetic skins. This is a material developed by SRI International, California. Under electrostatic effect, it changes stiffness. So under zero voltage, the material is compliant, it's floppy like paper. Then the button's pushed, a voltage is applied, and it becomes stiff as a board. \n(Tapping sounds) \nWe embed this material into the synthetic skin that attaches my bionic limb to my biological body. When I walk here, it's no voltage. My interface is soft and compliant. The button's pushed, voltage is applied, and it stiffens, offering me a greater maneuverability over the bionic limb. \nWe're also building exoskeletons. This exoskeleton becomes stiff and soft in just the right areas of the running cycle, to protect the biological joints from high impacts and degradation. In the future, we'll all be wearing exoskeletons in common activities, such as running. \n",
      title: 'The new bionics that let us run, climb and dance',
