      submittedBy: 'jason.carmel@possible.com',
      text:
        "And it turns out that if you went back to the 1870s, the most productive nation on earth was Australia, per person. And New Zealand was way up there. And then the U.S. came in about 1950, and then Switzerland about 1973, and then the U.S. got back on top -- beat up their chocolates and cuckoo clocks. And today, of course, you all know that the most productive nation on earth is Luxembourg, producing about one third more wealth per person per year than America. Tiny landlocked state. No oil. No diamonds. No natural resources. Just smart people moving bits. Different rules. \nHere's differential productivity rates. Here's how many people it takes to produce a single U.S. patent. So, about 3,000 Americans, 6,000 Koreans, 14,000 Brits, 790,000 Argentines. You want to know why Argentina's crashing? It's got nothing to do with inflation. It's got nothing to do with privatization. You can take a Harvard-educated Ivy League economist, stick him in charge of Argentina. He still crashes the country because he doesn't understand how the rules have changed. Oh, yeah, and it takes about 5.6 million Indians. Well, watch what happens to India. India and China used to be 40 percent of the global economy just at the Industrial Revolution, and they are now about 4.8 percent. Two billion people. One third of the global population producing 5 percent of the wealth because they didn't get this change, because they kept treating their people like serfs instead of like shareholders of a common project. They didn't keep the people who were educated. They didn't foment the businesses. They didn't do the IPOs. Silicon Valley did. And that's why they say that Silicon Valley has been powered by ICs. Not integrated circuits: Indians and Chinese. \n",
      title: 'The life code that will reshape the future',
