      created: {
        seconds: 1571266577,
        nanoseconds: 807000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "And here's an interesting map. This is a map which was finished at the Harvard Business School. One of the really interesting questions is, if all this data's free, who's using it? This is the greatest public library in the world. Well, it turns out that there's about 27 trillion bits moving inside from the United States to the United States; about 4.6 trillion is going over to those European countries; about 5.5's going to Japan; there's almost no communication between Japan, and nobody else is literate in this stuff. It's free. No one's reading it. They're focusing on the war; they're focusing on Bush; they're not interested in life. So, this is what a new map of the world looks like. That is the genomically literate world. And that is a problem. In fact, it's not a genomically literate world. You can break this out by states. And you can watch states rise and fall depending on their ability to speak a language of life, and you can watch New York fall off a cliff, and you can watch New Jersey fall off a cliff, and you can watch the rise of the new empires of intelligence. And you can break it out by counties, because it's specific counties. And if you want to get more specific, it's actually specific zip codes. (Laughter) \nSo, you want to know where life is happening? Well, in Southern California it's happening in 92121. And that's it. And that's the triangle between Salk, Scripps, UCSD, and it's called Torrey Pines Road. That means you don't need to be a big nation to be successful; it means you don't need a lot of people to be successful; and it means you can move most of the wealth of a country in about three or four carefully picked 747s. \nSame thing in Massachusetts. Looks more spread out but -- oh, by the way, the ones that are the same color are contiguous. What's the net effect of this? In an agricultural society, the difference between the richest and the poorest, the most productive and the least productive, was five to one. Why? Because in agriculture, if you had 10 kids and you grow up a little bit earlier and you work a little bit harder, you could produce about five times more wealth, on average, than your neighbor. In a knowledge society, that number is now 427 to 1. It really matters if you're literate, not just in reading and writing in English and French and German, but in Microsoft and Linux and Apple. And very soon it's going to matter if you're literate in life code. So, if there is something you should fear, it's that you're not keeping your eye on the ball. Because it really matters who speaks life. That's why nations rise and fall. \n",
      title: 'The life code that will reshape the future',
      uid: '89IV4qglRZTE9Ty8y9113yC7w9A3',
