      submittedBy: 'jason.carmel@possible.com',
      text:
        "The first thing that we ever read was in 1995: a little bacteria called Haemophilus influenzae. In the measure that you have the source code, as all of you know, you can change the source code, and you can reprogram life forms so that this little thingy becomes a vaccine, or this little thingy starts producing biomaterials, which is why DuPont is now growing a form of polyester that feels like silk in corn. This changes all rules. This is life, but we're reprogramming it. This is what you look like. This is one of your chromosomes. And what you can do now is, you can outlay exactly what your chromosome is, and what the gene code on that chromosome is right here, and what those genes code for, and what animals they code against, and then you can tie it to the literature. And in the measure that you can do that, you can go home today, and get on the Internet, and access the world's biggest public library, which is a library of life. And you can do some pretty strange things because in the same way as you can reprogram this apple, if you go to Cliff Tabin's lab at the Harvard Medical School, he's reprogramming chicken embryos to grow more wings. Why would Cliff be doing that? He doesn't have a restaurant. (Laughter) \nThe reason why he's reprogramming that animal to have more wings is because when you used to play with lizards as a little child, and you picked up the lizard, sometimes the tail fell off, but it regrew. Not so in human beings: you cut off an arm, you cut off a leg -- it doesn't regrow. But because each of your cells contains your entire gene code, each cell can be reprogrammed, if we don't stop stem cell research and if we don't stop genomic research, to express different body functions. And in the measure that we learn how chickens grow wings, and what the program is for those cells to differentiate, one of the things we're going to be able to do is to stop undifferentiated cells, which you know as cancer, and one of the things we're going to learn how to do is how to reprogram cells like stem cells in such a way that they express bone, stomach, skin, pancreas. And you are likely to be wandering around -- and your children -- on regrown body parts in a reasonable period of time, in some places in the world where they don't stop the research. \n",
