      created: {
        seconds: 1571261823,
        nanoseconds: 896000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "The problem is that, if I'm this doc, and I get your CT scan today and you've got a two centimeter mass in your liver, and you come back to me in three months and it's three centimeters, did that drug help you or not? How do I know? Would it have been 10 centimeters, or am I giving you a drug with no benefit and significant cost? So, it's a fundamental problem. And, again, that's where these new technologies can come in. \nAnd so, the goal obviously is that you go into your doctor's office -- well, the ultimate goal is that you prevent disease, right? The ultimate goal is that you prevent any of these things from happening. That is the most effective, cost-effective, best way we can do things today. But if one is unfortunate to get a disease, you'll go into your doctor's office, he or she will take a drop of blood, and we will start to know how to treat your disease. The way we've approached it is the field of proteomics, again, this looking at the system. It's taking a big picture. \nThe problem with technologies like this is that if one looks at proteins in the body, there are 11 orders of magnitude difference between the high-abundant and the low-abundant proteins. So, there's no technology in the world that can span 11 orders of magnitude. And so, a lot of what has been done with people like Danny Hillis and others is to try to bring in engineering principles, try to bring the software. We can start to look at different components along this spectrum. \n",
      title: 'A new strategy in the war on cancer',
      uid: '89IV4qglRZTE9Ty8y9113yC7w9A3',
      videoEndTime: '00:21:04',
      videoStartTime: '00:19:34',
