      created: {
        seconds: 1571261274,
        nanoseconds: 97000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "And so, if you think of cancer as a system, there's an input and an output and a state in the middle. So, the states, are equivalent classes of history, and the cancer patient, the input, is the environment, the diet, the treatment, the genetic mutations. The output are our symptoms: Do we have pain? Is the cancer growing? Do we feel bloated, etc.? Most of that state is hidden. So what we do in our field is we change and input, we give aggressive chemotherapy, and we say, \"Did that output get better? Did that pain improve, etc.?\" \nAnd so, the problem is that it's not just one system, it's multiple systems on multiple scales. It's a system of systems. And so, when you start to look at emergent systems, you can look at a neuron under a microscope. A neuron under the microscope is very elegant with little things sticking out and little things over here, but when you start to put them together in a complex system, and you start to see that it becomes a brain, and that brain can create intelligence, what we're talking about in the body, and cancer is starting to model it like a complex system. Well, the bad news is that these robust -- and robust is a key word -- emergent systems are very hard to understand in detail. The good news is you can manipulate them. You can try to control them without that fundamental understanding of every component.\n",
      title: 'A new strategy in the war on cancer',
      uid: '89IV4qglRZTE9Ty8y9113yC7w9A3',
      videoEndTime: '00:12:37',
      videoStartTime: '00:11:07',
