      created: {
        seconds: 1571261174,
        nanoseconds: 28000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "You know, I was very fortunate to meet Danny Hillis a few years ago. We were pushed together, and neither one of us really wanted to meet the other. I said, \"Do I really want to meet a guy from Disney, who designed computers?\" And he was saying: Does he really want to meet another doctor? But people prevailed on us, and we got together, and it's been transformative in what I do, absolutely transformative. We have designed, and we have worked on the modeling -- and much of these ideas came from Danny and from his team -- the modeling of cancer in the body as complex system. And I'll show you some data there where I really think it can make a difference and a new way to approach it. \nThe key is, when you look at these variables and you look at this data, you have to understand the data inputs. You know, if I measured your temperature over 30 days, and I asked, \"What was the average temperature?\" and it came back at 98.7, I would say, \"Great.\" But if during one of those days your temperature spiked to 102 for six hours, and you took Tylenol and got better, etc., I would totally miss it. So, one of the problems, the fundamental problems in medicine is that you and I, and all of us, we go to our doctor once a year. We have discrete data elements; we don't have a time function on them. \nEarlier it was referred to this direct life device. You know, I've been using it for two and a half months. It's a staggering device, not because it tells me how many kilocalories I do every day, but because it looks, over 24 hours, what I've done in a day. And I didn't realize that for three hours I'm sitting at my desk, and I'm not moving at all. And a lot of the functions in the data that we have as input systems here are really different than we understand them, because we're not measuring them dynamically. \n",
      title: 'A new strategy in the war on cancer',
