      created: {
        seconds: 1571260495,
        nanoseconds: 557000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "You know, I know very well, as a cancer doctor, I can't treat advanced cancer. So, as an aside, I firmly believe in the field of trying to identify cancer early. It is the only way you can start to fight cancer, is by catching it early. We can prevent most cancers. You know, the previous talk alluded to preventing heart disease. We could do the same in cancer. I co-founded a company called Navigenics, where, if you spit into a tube -- and we can look look at 35 or 40 genetic markers for disease, all of which are delayable in many of the cancers -- you start to identify what you could get, and then we can start to work to prevent them. Because the problem is, when you have advanced cancer, we can't do that much today about it, as the statistics allude to. \nSo, the thing about cancer is that it's a disease of the aged. Why is it a disease of the aged? Because evolution doesn't care about us after we've had our children. See, evolution protected us during our childbearing years and then, after age 35 or 40 or 45, it said \"It doesn't matter anymore, because they've had their progeny.\" So if you look at cancers, it is very rare -- extremely rare -- to have cancer in a child, on the order of thousands of cases a year. As one gets older? Very, very common. \nWhy is it hard to treat? Because it's heterogeneous, and that's the perfect substrate for evolution within the cancer. It starts to select out for those bad, aggressive cells, what we call clonal selection. But, if we start to understand that cancer isn't just a molecular defect, it's something more, then we'll get to new ways of treating it, as I'll show you.\n",
      title: 'A new strategy in the war on cancer',
      uid: '89IV4qglRZTE9Ty8y9113yC7w9A3',
