      submittedBy: 'jason.carmel@possible.com',
      text:
        "I'll close with regenerative medicine. I've studied a lot about stem cells. Embryonic stem cells are particularly powerful. We have adult stem cells throughout our body; we use those in bone marrow transplantation. Geron, last year, started the first trial using human embryonic stem cells to treat spinal cord injuries. Still a phase I trial, but evolving. We've been using adult stem cells in clinical trials for about 15 years to approach a whole range of topics, particularly cardiovascular disease. If we take our own bone marrow cells and treat a patient with a heart attack, we can see much improved heart function and better survival using our own bone marrow derived cells after a heart attack. \nI invented a device called the MarrowMiner, a much less invasive way for harvesting bone marrow. It's now been FDA approved; hopefully on the market in the next year. Hopefully you can appreciate the device going through the patient's body removing bone marrow, not with 200 punctures, but with a single puncture, under local anesthesia. \nWhere is stem-cell therapy going? If you think about it, every cell in your body has the same DNA you had when you were an embryo. We can now reprogram your skin cells to actually act like a pluripotent embryonic stem cell and utilize those, potentially, to treat multiple organs in the same patient, making personalized stem cell lines. I think there'll be a new era of your own stem cell banking to have in the freezer your own cardiac cells, myocytes and neural cells to use them in the future, should you need them. We're integrating this now with a whole era of cellular engineering, and integrating exponential technologies for essentially 3D organ printing, replacing the ink with cells, and essentially building and reconstructing a 3D organ. \n",
      title: "Medicine's future? There's an app for that",
      uid: '89IV4qglRZTE9Ty8y9113yC7w9A3',
