      created: {
        seconds: 1571259232,
        nanoseconds: 861000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "Now how about exponentially cheaper? Not something we usually think about in the era of medicine, but hard disks used to be 3,400 dollars for 10 megabytes -- exponentially cheaper. In genomics now, the genome cost about a billion dollars about 10 years ago, when the first one came out. We're now approaching essentially a $1,000 genome, probably next year. And in two years, a $100 genome. What will we do with $100 genomes? Soon we'll have millions of these tests available. Then it gets interesting, when we start to crowd-source that information, and enter the era of true personalized medicine: the right drug for the right person at the right time, instead of what we're doing now, which is the same drug for everybody, blockbuster drug medications, which don't work for the individual. Many different companies are working on leveraging these approaches. \nI'll show you a simple example, from 23andMe again. My data indicates I've got about average risk for developing macular degeneration, a kind of blindness. But if I take that same data, upload it to deCODEme, I can look at my risk for type 2 diabetes; I'm at almost twice the risk. I might want to watch how much dessert I have at lunch, for example. It might change my behavior. Leveraging my knowledge of my pharmacogenomics: how my genes modulate, what my drugs do and what doses I need will become increasingly important, and once in the hands of individuals and patients, will make better drug dosing and selection available.\n",
