      created: {
        seconds: 1571258856,
        nanoseconds: 619000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "Now, how about controlling other elements? For those who have disabilities -- the paraplegic, there's the brain-computer interface, or BCI, where chips have been put on the motor cortex of completely quadriplegic patients, and they can control a cursor or a wheelchair or, potentially, a robotic arm. These devices are getting smaller and going into more and more of these patients. Still in clinical trials, but imagine when we can connect these, for example, to the amazing bionic limb, such as the DEKA Arm, built by Dean Kamen and colleagues, which has 17 degrees of motion and freedom, and can allow the person who's lost a limb to have much higher dexterity or control than they've had in the past. \nSo we're really entering the era of wearable robotics, actually. If you haven't lost a limb but had a stroke, you can wear these augmented limbs. Or if you're a paraplegic -- I've visited the folks at Berkeley Bionics -- they've developed eLEGS. I took this video last week. Here's a paraplegic patient, walking by strapping on these exoskeletons. He's otherwise completely wheelchair-bound. This is the early era of wearable robotics. And by leveraging these sorts of technologies, we're going to change the definition of disability to, in some cases, be superability, or super-enabling. This is Aimee Mullins, who lost her lower limbs as a young child, and Hugh Herr, who's a professor at MIT, who lost his limbs in a climbing accident. And now both of them can climb better, move faster, swim differently with their prosthetics than us normal-abled persons. \n",
      title: "Medicine's future? There's an app for that",
