      created: {
        seconds: 1571257891,
        nanoseconds: 435000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "Now, I mentioned the phone. These mobile phones have over 20,000 different mobile apps available. There's one out of the UK where you can pee on a little chip, connect it to your iPhone, and check for an STD. I don't know if I'd try that, but it's available. There are other sorts of applications. Merging your phone and diagnostics, for example, measuring your blood glucose on your iPhone and sending that to your physician, so they can better understand and you can better understand your blood sugars as a diabetic. So let's see how exponential technologies are taking health care. Let's start with faster. It's no secret that computers, through Moore's law, are speeding up faster and faster. \nWe can do more powerful things with them. They're really approaching -- in many cases, surpassing -- the ability of the human mind. But where I think computational speed is most applicable is in imaging. The ability now to look inside the body in real time with very high resolution is really becoming incredible. And we're layering multiple technologies -- PET scans, CT scans and molecular diagnostics -- to find and seek things at different levels. Here you're going to see the very highest resolution MRI scan done today, of Marc Hodosh, the curator of TEDMED. And now we can see inside of the brain at a resolution and ability never before available, and essentially learn how to reconstruct and maybe even reengineer or backwards engineer the brain, so we can better understand pathology, disease and therapy. We can look inside with real-time fMRI in the brain at real time. And by understanding these sorts of processes and these connections, we're going to understand the effects of medication or meditation and better personalize and make effective, for example, psychoactive drugs. \n",
      title: "Medicine's future? There's an app for that",
