      submittedBy: 'jason.carmel@possible.com',
      text:
        "And the long transition is already well under way. In fact, three years ago mainstream analysts were starting to see peak oil, not in supply, but in demand. And Deutsche Bank even said world oil use could peak around 2016. \nIn other words, oil is getting uncompetitive even at low prices before it becomes unavailable even at high prices. But the electrified vehicles don't need to burden the electricity grid. Rather, when smart autos exchange electricity and information through smart buildings with smart grids, they're adding to the grid valuable flexibility and storage that help the grid integrate varying solar and wind power. \n",
