      submittedBy: 'jason.carmel@possible.com',
      text:
        "And just in the first two years the biggest of Europe's five feebate programs has tripled the speed of improving automotive efficiency. The resulting shift to electric autos is going to be as game-changing as shifting from typewriters to the gains in computers. Of course, computers and electronics are now America's biggest industry, while typewriter makers have vanished. So vehicle fitness opens a new automotive competitive strategy that can double the oil savings over the next 40 years, but then also make electrification affordable, and that displaces the rest of the oil. \nAmerica could lead this next automotive revolution. Currently the leader is Germany. Last year, Volkswagen announced that by next year they'll be producing this carbon fiber plugin hybrid getting 230 miles a gallon. Also last year, BMW announced this carbon fiber electric car, they said that its carbon fiber is paid for by needing fewer batteries. And they said, \"We do not intend to be a typewriter maker.\" Audi claimed it's going to beat them both by a year. \n",
