      created: {
        seconds: 1571251351,
        nanoseconds: 981000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        'Unfortunately, over the past quarter century, epidemic obesity has made our two-ton steel cars gain weight twice as fast as we have. But today, ultralight, ultrastrong materials, like carbon fiber composites, can make dramatic weight savings snowball and can make cars simpler and cheaper to build. Lighter and more slippery autos need less force to move them, so their engines get smaller. Indeed, that sort of vehicle fitness then makes electric propulsion affordable because the batteries or fuel cells also get smaller and lighter and cheaper. So sticker prices will ultimately fall to about the same as today, while the driving cost, even from the start, is very much lower. \nSo these innovations together can transform automakers from wringing tiny savings out of Victorian engine and seal-stamping technologies to the steeply falling costs of three linked innovations that strongly reenforce each other -- namely ultralight materials, making them into structures and electric propulsion. The sales can grow and the prices fall even faster with temporary feebates, that is rebates for efficient new autos paid for by fees on inefficient ones. \n',
      title: 'A 40-year plan for energy',
      uid: '89IV4qglRZTE9Ty8y9113yC7w9A3',
