      submittedBy: 'jason.carmel@possible.com',
      text:
        "Chris Anderson: So your idea worth spreading is hire hackers. How would someone get over that kind of fear that the hacker they hire might preserve that little teensy wormhole? \nMG: I think to an extent, you have to understand that it's axiomatic among hackers that they do that. They're just relentless and obsessive about what they do. But all of the people who I've spoken to who have fallen foul of the law, they have all said, \"Please, please give us a chance to work in the legitimate industry. We just never knew how to get there, what we were doing. We want to work with you.\" \nChris Anderson: Okay, well that makes sense. Thanks a lot Misha. \n",
      title: 'Hire the hackers!',
