      submittedBy: 'jason.carmel@possible.com',
      text:
        "Now this is a very un-TED-like thing to do, but let's kick off the afternoon with a message from a mystery sponsor. \nAnonymous: Dear Fox News, it has come to our unfortunate attention that both the name and nature of Anonymous has been ravaged. We are everyone. We are no one. We are anonymous. We are legion. We do not forgive. We do not forget. We are but the base of chaos. \nMisha Glenny: Anonymous, ladies and gentlemen -- a sophisticated group of politically motivated hackers who have emerged in 2011. And they're pretty scary. You never know when they're going to attack next, who or what the consequences will be. But interestingly, they have a sense of humor. These guys hacked into Fox News' Twitter account to announce President Obama's assassination. Now you can imagine the panic that would have generated in the newsroom at Fox. \"What do we do now? Put on a black armband, or crack open the champagne?\" (Laughter) And of course, who could escape the irony of a member of Rupert Murdoch's News Corp. being a victim of hacking for a change. \n",
      title: 'Hire the hackers!',
