      created: {
        seconds: 1571243693,
        nanoseconds: 517000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "So it is wholesale blanket surveillance of all of us, all of us who use telecommunications and the Internet. \nBut don't get me wrong: There are actually types of surveillance that are okay. I love freedom, but even I agree that some surveillance is fine. If the law enforcement is trying to find a murderer, or they're trying to catch a drug lord or trying to prevent a school shooting, and they have leads and they have suspects, then it's perfectly fine for them to tap the suspect's phone, and to intercept his Internet communications. I'm not arguing that at all, but that's not what programs like PRISM are about. They are not about doing surveillance on people that they have reason to suspect of some wrongdoings. They're about doing surveillance on people they know are innocent. \n",
