      created: {
        seconds: 1571243194,
        nanoseconds: 631000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "And from this information, we can begin to map the hope spots. So this is just three years of data right here -- and there's a decade of this data. We see the pulse and the seasonal activities that these animals are going on. So what we're able to do with this information is boil it down to hot spots, 4,000 deployments, a huge herculean task, 2,000 tags in an area, shown here for the first time, off the California coast, that appears to be a gathering place. And then for sort of an encore from these animals, they're helping us. They're carrying instruments that are actually taking data down to 2,000 meters. They're taking information from our planet at very critical places like Antarctica and the Poles. Those are seals from many countries being released who are sampling underneath the ice sheets and giving us temperature data of oceanographic quality on both poles. \nThis data, when visualized, is captivating to watch. We still haven't figured out best how to visualize the data. And then, as these animals swim and give us the information that's important to climate issues, we also think it's critical to get this information to the public, to engage the public with this kind of data. We did this with the Great Turtle Race -- tagged turtles, brought in four million hits. And now with Google's Oceans, we can actually put a white shark in that ocean. And when we do and it swims, we see this magnificent bathymetry that the shark knows is there on its path as it goes from California to Hawaii. But maybe Mission Blue can fill in that ocean that we can't see. We've got the capacity, NASA has the ocean. We just need to put it together. \n",
      title: 'Tagging tuna in the deep ocean',
