      created: {
        seconds: 1571243003,
        nanoseconds: 482000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "Now in a second project called Tagging of Pacific Pelagics, we took on the planet as a team, those of us in the Census of Marine Life. And, funded primarily through Sloan Foundation and others, we were able to actually go in, in our project -- we're one of 17 field programs and begin to take on tagging large numbers of predators, not just tunas. So what we've done is actually gone up to tag salmon shark in Alaska, met salmon shark on their home territory, followed them catching salmon and then went in and figured out that, if we take a salmon and put it on a line, we can actually take up a salmon shark -- This is the cousin of the white shark -- and very carefully -- note, I say \"very carefully,\" -- we can actually keep it calm, put a hose in its mouth, keep it off the deck and then tag it with a satellite tag. That satellite tag will now have your shark phone home and send in a message. And that shark leaping there, if you look carefully, has an antenna. It's a free swimming shark with a satellite tag jumping after salmon, sending home its data. Salmon sharks aren't the only sharks we tag. But there goes salmon sharks with this meter-level resolution on an ocean of temperature -- warm colors are warmer. Salmon sharks go down to the tropics to pup and come into Monterey. \nNow right next door in Monterey and up at the Farallones are a white shark team led by Scott Anderson -- there -- and Sal Jorgensen. They can throw out a target -- it's a carpet shaped like a seal -- and in will come a white shark, a curious critter that will come right up to our 16-ft. boat. It's a several thousand-pound animal. And we'll wind in the target. And we'll place an acoustic tag that says, \"OMSHARK 10165,\" or something like that, acoustically with a ping. And then we'll put on a satellite tag that will give us the long-distance journeys with the light-based geolocation algorithms solved on the computer that's on the fish. So in this case, Sal's looking at two tags there, and there they are: the white sharks of California going off to the white shark cafe and coming back. We also tag makos with our NOAA colleagues, blue sharks. And now, together, what we can see on this ocean of color that's temperature, we can see ten-day worms of makos and salmon sharks. We have white sharks and blue sharks. For the first time, an ecoscape as large as ocean-scale, showing where the sharks go. \n",
