      created: {
        seconds: 1571242702,
        nanoseconds: 649000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "So the story of our team, a dedicated team, is fish and chips. We basically are taking the same satellite phone parts, or the same parts that are in your computer, chips. We're putting them together in unusual ways, and this is taking us into the ocean realm like never before. And for the first time, we're able to watch the journey of a tuna beneath the ocean using light and photons to measure sunrise and sunset. Now, I've been working with tunas for over 15 years. I have the privilege of being a partner with the Monterey Bay Aquarium. We've actually taken a sliver of the ocean, put it behind glass, and we together have put bluefin tuna and yellowfin tuna on display. When the veil of bubbles lifts every morning, we can actually see a community from the Pelagic ocean, one of the only places on Earth you can see giant bluefin swim by. We can see in their beauty of form and function, their ceaseless activity. They're flying through their space, ocean space. And we can bring two million people a year into contact with this fish and show them its beauty. \nBehind the scenes is a working lab at Stanford University partnered with the Monterey Bay Aquarium. Here, for over 14 or 15 years, we've actually brought in both bluefin and yellowfin in captivity. We'd been studying these fish, but first we had to learn how to husbandry them. What do they like to eat? What is it that they're happy with? We go in the tanks with the tuna -- we touch their naked skin -- it's pretty amazing. It feels wonderful. And then, better yet, we've got our own version of tuna whisperers, our own Chuck Farwell, Alex Norton, who can take a big tuna and in one motion, put it into an envelope of water, so that we can actually work with the tuna and learn the techniques it takes to not injure this fish who never sees a boundary in the open sea. Jeff and Jason there, are scientists who are going to take a tuna and put it in the equivalent of a treadmill, a flume. And that tuna thinks it's going to Japan, but it's staying in place. We're actually measuring its oxygen consumption, its energy consumption. We're taking this data and building better models. And when I see that tuna -- this is my favorite view -- I begin to wonder: how did this fish solve the longitude problem before we did? So take a look at that animal. That's the closest you'll probably ever get. Now, the activities from the lab have taught us now how to go out in the open ocean. \n",
      title: 'Tagging tuna in the deep ocean',
