      created: {
        seconds: 1571242552,
        nanoseconds: 883000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "Now, bluefin were revered by Man for all of human history. For 4,000 years, we fished sustainably for this animal, and it's evidenced in the art that we see from thousands of years ago. Bluefin are in cave paintings in France. They're on coins that date back 3,000 years. This fish was revered by humankind. It was fished sustainably till all of time, except for our generation. Bluefin are pursued wherever they go -- there is a gold rush on Earth, and this is a gold rush for bluefin. There are traps that fish sustainably up until recently. And yet, the type of fishing going on today, with pens, with enormous stakes, is really wiping bluefin ecologically off the planet. Now bluefin, in general, goes to one place: Japan. Some of you may be guilty of having contributed to the demise of bluefin. They're delectable muscle, rich in fat -- absolutely taste delicious. And that's their problem; we're eating them to death. Now in the Atlantic, the story is pretty simple. Bluefin have two populations: one large, one small. The North American population is fished at about 2,000 ton. The European population and North African -- the Eastern bluefin tuna -- is fished at tremendous levels: 50,000 tons over the last decade almost every year. \nThe result is whether you're looking at the West or the Eastern bluefin population, there's been tremendous decline on both sides, as much as 90 percent if you go back with your baseline to 1950. For that, bluefin have been given a status equivalent to tigers, to lions, to certain African elephants and to pandas. These fish have been proposed for an endangered species listing in the past two months. They were voted on and rejected just two weeks ago, despite outstanding science that shows from two committees this fish meets the criteria of CITES I. And if it's tunas you don't care about, perhaps you might be interested that international long lines and pursing chase down tunas and bycatch animals such as leatherbacks, sharks, marlin, albatross. These animals and their demise occurs in the tuna fisheries. The challenge we face is that we know very little about tuna, and everyone in the room knows what it looks like when an African lion takes down its prey. I doubt anyone has seen a giant bluefin feed. This tuna symbolizes what's the problem for all of us in the room. \n",
