      created: {
        seconds: 1571241888,
        nanoseconds: 853000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "So we figured out some pretty interesting ways to track CO2 levels in the atmosphere, going back millions of years. We used to do it just with ice cores, but in this case, we're going back 20 million years. And we take samples of the sediment, and it tells us the CO2 level of the ocean, and therefore the CO2 level of the atmosphere. And here's the thing: you have to go back about 15 million years to find a time when CO2 levels were about what they are today. You have to go back about 30 million years to find a time when CO2 levels were double what they are today. Now, what that means is that all of the organisms that live in the sea have evolved in this chemostatted ocean, with CO2 levels lower than they are today. That's the reason that they're not able to respond or adapt to this rapid acidification that's going on right now. \nSo, Charlie Veron came up with this statement last year: \"The prospect of ocean acidification may well be the most serious of all of the predicted outcomes of anthropogenic CO2 release.\" And I think that may very well be true, so I'll close with this. You know, we do need the protected areas, absolutely, but for the sake of the oceans, we have to cap or limit CO2 emissions as soon as possible. \nThank you very much. \n",
