      created: {
        seconds: 1571241810,
        nanoseconds: 308000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "So I'll close with a short discussion of ocean acidification. I like it as a component of global change to talk about, because, even if you are a hard-bitten global warming skeptic, and I talk to that community fairly often, you cannot deny the simple physics of CO2 dissolving in the ocean. You know, we're pumping out lots of CO2 into the atmosphere, from fossil fuels, from cement production. Right now, about a third of that carbon dioxide is dissolving straight into the sea, right? And as it does so, it makes the ocean more acidic. So, you cannot argue with that. That is what's happening right now, and it's a very different issue than the global warming issue. It has many consequences. \nThere's consequences for carbonate organisms. There are many organisms that build their shells out of calcium carbonate -- plants and animals both. The main framework material of coral reefs is calcium carbonate. That material is more soluble in acidic fluid. So one of the things we're seeing is organisms are having to spend more metabolic energy to build and maintain their shells. At some point, as this transience, as this CO2 uptake in the ocean continues, that material's actually going to start to dissolve. And on coral reefs, where some of the main framework organisms disappear, we will see a major loss of marine biodiversity. But it's not just the carbonate producers that are affected. There's many physiological processes that are influenced by the acidity of the ocean. So many reactions involving enzymes and proteins are sensitive to the acid content of the ocean. So, all of these things -- greater metabolic demands, reduced reproductive success, changes in respiration and metabolism. You know, these are things that we have good physiological reasons to expect to see stressed caused by this transience. \n",
      title: 'Discovering ancient climates in oceans and ice',
