      created: {
        seconds: 1571241742,
        nanoseconds: 395000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "In this case, at these islands, you know, corals are instrumental-quality recorders of change in the water. And of course, our thermometers only take us back 50 years or so here. The coral can take us back hundreds and thousands of years. So, what we do: we've merged a lot of different data sets. It's not just my group; there's maybe 30 groups worldwide doing this. But we get these instrumental- and near-instrumental-quality records of temperature change that go back hundreds of years, and we put them together. Here's a synthetic diagram. There's a whole family of curves here. \nBut what's happening: we're looking at the last thousand years of temperature on the planet. And there's five or six different compilations there, But each one of those compilations reflects input from hundreds of these kinds of records from corals. We do similar things with ice cores. We work with tree rings. And that's how we discover what is truly natural and how different is the last century, right? And I chose this one because it's complicated and messy looking, right. This is as messy as it gets. You can see there's some signals there. Some of the records show lower temperatures than others. Some of them show greater variability. But they all tell us what the natural variability is. Some of them are from the northern hemisphere; some are from the entire globe. \nBut here's what we can say: what's natural in the last thousand years is that the planet was cooling down. It was cooling down until about 1900 or so. And there is natural variability caused by the Sun, caused by El Ninos. A century-scale, decadal-scale variability, and we know the magnitude; it's about two-tenths to four-tenths of a degree Centigrade. But then at the very end is where we have the instrumental record in black. And there's the temperature up there in 2009. You know, we've warmed the globe about a degree Centigrade in the last century, and there's nothing in the natural part of that record that resembles what we've seen in the last century. You know, that's the strength of our argument, that we are doing something that's truly different.\n",
      title: 'Discovering ancient climates in oceans and ice',
