      created: {
        seconds: 1571241545,
        nanoseconds: 189000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "Okay, the second story takes place here in Galapagos. This is a bleached coral, coral that died during the 1982-'83 El Nino. This is from Champion Island. It's about a meter tall Pavona clavus colony. And it's covered with algae. That's what happens. When these things die, immediately, organisms come in and encrust and live on that dead surface. And so, when a coral colony is killed by an El Nino event, it leaves this indelible record. You can go then and study corals and figure out how often do you see this. So one of the things thought of in the '80s was to go back and take cores of coral heads throughout the Galapagos and find out how often was there a devastating event. And just so you know, 1982-'83, that El Nino killed 95 percent of all the corals here in Galapagos. Then there was similar mortality in '97-'98. And what we found after drilling back in time two to 400 years was that these were unique events. We saw no other mass mortality events. So these events in our recent past really are unique. So they're either just truly monster El Ninos, or they're just very strong El Ninos that occurred against a backdrop of global warming. Either case, it's bad news for the corals of the Galapagos Islands. \nHere's how we sample the corals. This is actually Easter Island. Look at this monster. This coral is eight meters tall, right. And it been growing for about 600 years. Now, Sylvia Earle turned me on to this exact same coral. And she was diving here with John Lauret -- I think it was 1994 -- and collected a little nugget and sent it to me. And we started working on it, and we figured out we could tell the temperature of the ancient ocean from analyzing a coral like this. So we have a diamond drill. We're not killing the colony; we're taking a small core sample out of the top. The core comes up as these cylindrical tubes of limestone. And that material then we take back to the lab and analyze it. You can see some of the coral cores there on the right. \n",
      title: 'Discovering ancient climates in oceans and ice',
