      created: {
        seconds: 1571241464,
        nanoseconds: 555000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "It took about 10 years to put this project together. And here's what we found. Now, there's 40 scientists working on this project, and people are doing all kinds of really complicated and expensive analyses. But it turns out, you know, the thing that told the best story was this simple visual description. You know, we saw this in the core samples as they came up. We saw these alternations between sediments that look like this -- there's gravel and cobbles in there and a bunch of sand. That's the kind of material in the deep sea. It can only get there if it's carried out by ice. So we know there's an ice shelf overhead. And that alternates with a sediment that looks like this. This is absolutely beautiful stuff. This sediment is 100 percent made up of the shells of microscopic plants. And these plants need sunlight, so we know when we find that sediment there's no ice overhead. And we saw about 35 alternations between open water and ice-covered water, between gravels and these plant sediments. \nSo what that means is, what it tells us is that the Ross Sea region, this ice shelf, melted back and formed anew about 35 times. And this is in the past four million years. This was completely unexpected. Nobody imagined that the West Antarctic Ice Sheet was this dynamic. In fact, the lore for many years has been, \"The ice formed many tens of millions of years ago, and it's been there ever since.\" And now we know that in our recent past it melted back and formed again, and sea level went up and down, six meters at a time. \nWhat caused it? Well, we're pretty sure that it's very small changes in the amount of sunlight reaching Antarctica, just caused by natural changes in the orbit of the Earth. But here's the key thing: you know, the other thing we found out is that the ice sheet passed a threshold, that the planet warmed up enough -- and the number's about one degree to one and a half degrees Centigrade -- the planet warmed up enough that it became ... that ice sheet became very dynamic and was very easily melted. And you know what? We've actually changed the temperature in the last century just the right amount. So many of us are convinced now that West Antarctica, the West Antarctic Ice Sheet, is starting to melt. We do expect to see a sea-level rise on the order of one to two meters by the end of this century. And it could be larger than that. This is a serious consequence for nations like Kiribati, you know, where the average elevation is about a little over a meter above sea level. \n",
