      submittedBy: 'jason.carmel@possible.com',
      text:
        "One of the problems is coming up with the right target. It's not clear what the target should be. And how can you figure out how to fix something if you don't have a clear target? Now, you've heard about \"two degrees\": that we should limit temperature rise to no more than two degrees. But there's not a lot of science behind that number. We've also talked about concentrations of carbon dioxide in the atmosphere. Should it be 450? Should it be 400? There's not a lot of science behind that one either. Most of the science that is behind these numbers, these potential targets, is based on studies on land. And I would say, for the people that work in the ocean and think about what the targets should be, we would argue that they must be much lower. You know, from an oceanic perspective, 450 is way too high. Now there's compelling evidence that it really needs to be 350. We are, right now, at 390 parts per million of CO2 in the atmosphere. We're not going to put the brakes on in time to stop at 450, so we've got to accept we're going to do an overshoot, and the discussion as we go forward has to focus on how far the overshoot goes and what's the pathway back to 350. \nNow, why is this so complicated? Why don't we know some of these things a little bit better? Well, the problem is that we've got very complicated forces in the climate system. There's all kinds of natural causes of climate change. There's air-sea interactions. Here in Galapagos, we're affected by El Ninos and La Nina. But the entire planet warms up when there's a big El Nino. Volcanoes eject aerosols into the atmosphere. That changes our climate. The ocean contains most of the exchangeable heat on the planet. So anything that influences how ocean surface waters mix with the deep water changes the ocean of the planet. And we know the solar output's not constant through time. So those are all natural causes of climate change. And then we have the human-induced causes of climate change as well. We're changing the characteristics of the surface of the land, the reflectivity. We inject our own aerosols into the atmosphere, and we have trace gases, and not just carbon dioxide -- it's methane, ozone, oxides of sulfur and nitrogen. \n",
      title: 'Discovering ancient climates in oceans and ice',
