      submittedBy: 'jason.carmel@possible.com',
      text:
        'I was in Copenhagen in December like a number of you in this room. And I think we all found it, simultaneously, an eye-opening and a very frustrating experience. I sat in this large negotiation hall, at one point, for three or four hours, without hearing the word "oceans" one time. It really wasn\'t on the radar screen. The nations that brought it up when we had the speeches of the national leaders -- it tended to be the leaders of the small island states, the low-lying island states. And by this weird quirk of alphabetical order of the nations, a lot of the low-lying states, like Kiribati and Nauru, they were seated at the very end of these immensely long rows. You know, they were marginalized in the negotiation room. \n',
