      created: {
        seconds: 1571239764,
        nanoseconds: 786000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "For a moment, what I need to do is project something on the screen of your imagination. We're in 17th century Japan on the west coast, and a little, wizened monk is hurrying along, near midnight, to the crest of a small hill. He arrives on the small hill, dripping with water. He stands there, and he looks across at the island, Sado. And he scans across the ocean, and he looks at the sky. Then he says to himself, very quietly, \"[Turbulent the sea,] [Stretching across to Sado] [The Milky Way].\" Basho was a brilliant man. He said more with less than any human that I have ever read or talked to. Basho, in 17 syllables, juxtaposed a turbulent ocean driven by a storm now past, and captured the almost impossible beauty of our home galaxy with millions of stars, probably hundreds and hundreds of -- who knows how many -- planets, maybe even an ocean that we will probably call Sylvia in time. As he was nearing his death, his disciples and followers kept asking him, \"What's the secret? How can you make haiku poems so beautiful so easily?\" And toward the end, he said, \"If you would know the pine tree, go to the pine tree.\" That was it. \n\nSylvia has said we must use every capacity we have in order to know the oceans. If we would know the oceans, we must go to the oceans. And what I'd like to talk to you today about, a little bit, is really transforming the relationship, or the interplay, between humans and oceans with a new capability that is not at all routine yet. I hope it will be. There are a few key points. One of them is the oceans are central to the quality of life on earth. Another is that there are bold, new ways of studying oceans that we have not used well yet. And the last is that these bold, new ways that we are exploring as a community will transform the way we look at our planet, our oceans, and eventually how we manage probably the entire planet, for what it's worth. So what scientists do when they begin is to start with the system. They define what the system is. The system isn't Chesapeake Bay. It's not the Kuril arc. It's not even the entire Pacific. It's the whole planet, the entire planet, continents and oceans together. That's the system. ",
      title: 'Wiring an interactive ocean',
