      submittedBy: 'jason.carmel@possible.com',
      text:
        'Title: Visualizing ourselves ... with crowd-sourced data\n\nText: So the last project I want to talk to you about is another collaboration with Chris Milk. And this is called "The Wilderness Downtown." It\'s an online music video for the Arcade Fire. Chris and I were really amazed by the potential now with modern web browsers, where you have HTML5 audio and video and the power of JavaScript to render amazingly fast. And we wanted to push the idea of the music video that was meant for the Web beyond the four-by-three or sixteen-by-nine window and try to make it play out and choreograph throughout the screen. But most importantly, I think, we really wanted to make an experience that was unlike the Johnny Cash Project, where you had a small group of people spending a lot of time to contribute something for everyone. What if we had a very low commitment, but delivered something individually unique to each person who contributed? So the project starts off by asking you to enter the address of the home where you grew up. And you type in the address -- it actually creates a music video specifically for you, pulling in Google maps and Streetview images into the experience itself. So this should really be seen at home with you typing in your own address, but I\'m going to give you a little preview of what you can expect. (Video) Win Butler: ♫ Now our lives are changing fast ♫ ♫ Now our lives are changing fast ♫ ♫ Hope that something pure can last ♫ ♫ Hope that something pure can last ♫ ♫ Ooh we used to wait ♫ ♫ Ooh we used to wait ♫ ♫ Ooh we used to wait ♫ ♫ Sometimes it never came ♫ ♫ Sometimes it never came ♫ ♫ Still moving through the pain ♫ ♫ We used to wait for it ♫ ♫ We used to wait for it ♫ ♫ We used to wait for it\n\nVideo Start Time: 00:15:51\n\nVideo End Time: 00:17:47\n\nSubmitted by jason.carmel@possible.com\n\n10/15/2019, 5:10:47 PM\nhttps://download.ted.com/talks/AaronKoblin_2011-light.mp4',
