      submittedBy: 'jason.carmel@possible.com',
      text:
        'So stepping back for a quick second, when I was at UCLA going to grad school, I was also working at a place called the Center for Embedded Network Sensing. And I was writing software to visualize laser scanners. So basically motion through 3D space. And this was seen by a director in L.A. named James Frost who said, "Wait a minute. You mean we can shoot a music video without actually using any video?" So we did exactly that. We made a music video for one of my favorite bands, Radiohead. And I think one of my favorite parts of this project was not just shooting a video with lasers, but we also open sourced it, and we made it released as a Google Code project, where people could download a bunch of the data and some source code to build their own versions of it. And people were making some amazing things. This is actually two of my favorites: the pin-board Thom Yorke and a LEGO Thom Yorke. A whole YouTube channel of people submitting really interesting content. More recently, somebody even 3D-printed Thom Yorke\'s head, which is a little creepy, but pretty cool. ',
      title: 'Visualizing ourselves ... with crowd-sourced data',
