      created: {
        seconds: 1571184145,
        nanoseconds: 643000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "So I created this drawing tool. I asked people to draw a sheep facing to the left. And I said, \"I'll pay you two cents for your contribution.\" And I started collecting sheep. And I collected a lot, a lot of different sheep. Lots of sheep. I took the first 10,000 sheep that I collected, and I put them on a website called TheSheepMarket.com where you can actually buy collections of 20 sheep. You can't pick individual sheep, but you can buy a single plate block of stamps as a commodity. And juxtaposed against this grid, you see actually, by rolling over each individual one, the humanity behind this hugely mechanical process. I think there's something really interesting to watching people as they go through this creative toil -- something we can all relate to, this creative process of trying to come up with something from nothing. I think it was really interesting to juxtapose this humanity versus this massive distributed grid. Kind of amazing what some people did. \nSo here's a few statistics from the project. Approximate collection rate of 11 sheep per hour, which would make a working wage of 69 cents per hour. There were 662 rejected sheep that didn't meet \"sheep-like\" criteria and were thrown out of the flock. (Laughter) The amount of time spent drawing ranged from four seconds to 46 minutes. That gives you an idea of the different types of motivations and dedication. And there were 7,599 people that contributed to the project, or were unique IP addresses -- so about how many people contributed. But only one of them out of the 7,599 said this. (Laughter) Which I was pretty surprised by. I expected people to be wondering, \"Why did I draw a sheep?\" And I think it's a pretty valid question. \nAnd there's a lot of reasons why I chose sheep. Sheep were the first animal to be raised from mechanically processed byproducts, the first to be selectively bred for production traits, the first animal to be cloned. Obviously, we think of sheep as followers. And there's this reference to \"Le Petit Prince\" where the narrator asks the prince to draw a sheep. He draws sheep after sheep. The narrator's only appeased when he draws a box. And he says, \"It's not about a scientific rendering of a sheep. It's about your own interpretation and doing something different.\" And I like that. \n",
