        "So this is another project that I worked on with the Sensible Cities Lab at MIT. This is visualizing international communications. So it's how New York communicates with other international cities. And we set this up as a live globe in the Museum of Modern Art in New York for the Design the Elastic Mind exhibition. And it had a live feed with a 24-hour offset, so you could see the changing relationship and some demographic info coming through AT&amp;T's data and revealing itself. This is another project I worked on with Sensible Cities Lab and CurrentCity.org. And it's visualizing SMS messages being sent in the city of Amsterdam. So you're seeing the daily ebb and flow of people sending SMS messages from different parts of the city, until we approach New Year's Eve, where everybody says, \"Happy New Year!\" \n (Laughter) \nSo this is an interactive tool that you can move around and see different parts of the city. This is looking at another event. This is called Queen's Day. So again, you get this daily ebb and flow of people sending SMS messages from different parts of the city. And then you're going to see people start to gather in the center of the city to celebrate the night before, which happens right here. And then you can see people celebrating the next day. And you can pause it and step back and forth and see different phases. \n",
      title: 'Visualizing ourselves ... with crowd-sourced data',
      uid: '89IV4qglRZTE9Ty8y9113yC7w9A3',
