      created: {
        seconds: 1571183886,
        nanoseconds: 677000000
      },
      submittedBy: 'jason.carmel@possible.com',
      text:
        "This is a project called Flight Patterns. What you're looking at is airplane traffic over North America for a 24-hour period. As you see, everything starts to fade to black, and you see people going to sleep. Followed by that, you see on the West coast planes moving across, the red-eye flights to the East coast. And you'll see everybody waking up on the East coast, followed by European flights coming in the upper right-hand corner. Everybody's moving from the East coast to the West coast. You see San Francisco and Los Angeles start to make their journeys down to Hawaii in the lower left-hand corner. \nI think it's one thing to say there's 140,000 planes being monitored by the federal government at any one time, and it's another thing to see that system as it ebbs and flows. This is a time-lapse image of that exact same data, but I've color-coded it by type, so you can see the diversity of aircraft that are in the skies above us. And I started making these, and I put them into Google Maps and allow you to zoom in and see individual airports and the patterns that are occurring there. So here we can see the white represents low altitudes, and the blue are higher altitudes. And you can zoom in. This is taking a look at Atlanta. You can see this is a major shipping airport, and there's all kinds of activity there. You can also toggle between altitude for model and manufacturer. See again, the diversity. And you can scroll around and see some of the different airports and the different patterns that they have. This is scrolling up the East coast. You can see some of the chaos that's happening in New York with the air traffic controllers having to deal with all those major airports next to each other. \nSo zooming back out real quick, we see, again, the U.S. -- you get Florida down in the right-hand corner. Moving across to the West coast, you see San Francisco and Los Angeles -- big low-traffic zones across Nevada and Arizona. And that's us down there in L.A. and Long Beach on the bottom. I started taking a look as well at different perimeters, because you can choose what you want to pull out from the data. This is looking at ascending versus descending flights. And you can see, over time, the ways the airports change. You see the holding patterns that start to develop in the bottom of the screen. And you can see, eventually the airport actually flips directions. \n",
      title: 'Visualizing ourselves ... with crowd-sourced data',
