      submittedBy: 'jason.carmel@possible.com',
      text:
        'The greatest danger that now faces liberal democracy is that the revolution in information technology will make dictatorships more efficient than democracies. \nIn the 20th century, democracy and capitalism defeated fascism and communism because democracy was better at processing data and making decisions. Given 20th-century technology, it was simply inefficient to try and concentrate too much data and too much power in one place. \nBut it is not a law of nature that centralized data processing is always less efficient than distributed data processing. With the rise of artificial intelligence and machine learning, it might become feasible to process enormous amounts of information very efficiently in one place, to take all the decisions in one place, and then centralized data processing will be more efficient than distributed data processing. And then the main handicap of authoritarian regimes in the 20th century -- their attempt to concentrate all the information in one place -- it will become their greatest advantage. \n',
      title: 'Why fascism is so tempting — and how your data could power it',
      uid: '89IV4qglRZTE9Ty8y9113yC7w9A3',
      videoEndTime: '00:10:09',
